import React from 'react'
import PropTypes from 'prop-types'
import './quick-facts.scss'
import { Grid, GridColumn, GridContainer } from '../Grid'
import { Title, TitleAccent } from '../Title'
import Divider from '../Divider'
import { Image } from '../Image'
import ArrowImage from '../../static/arrow.png'
import Cta from '../Cta'
import Facts from '../Facts'

const QuickFacts = ({fact}) => {
    return (
        <GridContainer className="quick-fact">
            <Grid>
                <Title className="quick-fact__title">
                    {fact.title}{' '}
                    <TitleAccent>{fact.subtitle}</TitleAccent>
                </Title>
            <GridColumn columns="5">
                <Facts />
            </GridColumn>
            <GridColumn columns="7">
                <Title className="quick-fact__content">{fact.content}</Title>
                <Title className="quick-fact__subcontent">{fact.subcontent}</Title>

                <Cta variant="link" to="/" className="quick-fact__link">
                    the challenge
                <Image src={ArrowImage} alt="down arrow" className="quick-fact__link-img" />
                </Cta>
            </GridColumn>
            </Grid>
            <Divider variant="complete" />
        </GridContainer>
    )
}

QuickFacts.propTypes = {
    fact:PropTypes.string,
    children:PropTypes.string
  }
export default QuickFacts;
