import PropTypes from 'prop-types'
import React from 'react'
import { Parallax, ParallaxProvider } from 'react-scroll-parallax'
import Cta from '../../components/Cta'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import IconArrowRight from '../../components/Icons/IconArrowRight'
import { BackgroundImage } from '../../components/Image'
import Paper from '../../components/Paper'
import { Title } from '../../components/Title'
import userGroupImage from '../../static/icon-users.png'
import './banner-callout.scss'

function BannerCallout({
  title,
  content,
  cta,
  image,
  variant,
  backgroundContent,
}) {
  return (
    <ParallaxProvider>
      <GridContainer className={`banner-callout banner-callout--${variant}`}>
        <Grid>
          <GridColumn columns="12">
            <BackgroundImage image={image} variant="banner">
              <Grid variant="middle" className="banner-callout__grid">
                {backgroundContent && (
                  <div className="banner-callout__text">
                    <img src={userGroupImage} alt="" />
                    {backgroundContent}
                  </div>
                )}

                <Parallax y={[-20, 20]}>
                  <Paper className="banner-callout__paper">
                    <Grid variant="row">
                      <Title
                        variant="cardTitleSmall"
                        className="banner-callout__title"
                      >
                        {title}
                      </Title>
                      <p>{content}</p>
                      <Cta
                        className="banner-callout__cta"
                        variant={cta.variant ? cta.variant : 'link'}
                        to={cta.url}
                      >
                        {cta.text}
                        <IconArrowRight />
                      </Cta>
                    </Grid>
                  </Paper>
                </Parallax>
              </Grid>
            </BackgroundImage>
          </GridColumn>
        </Grid>
      </GridContainer>
    </ParallaxProvider>
  )
}

BannerCallout.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  backgroundContent: PropTypes.string,
  variant: PropTypes.string,
  cta: PropTypes.shape({
    text: PropTypes.string,
    variant: PropTypes.string,
    url: PropTypes.string.isRequired,
  }).isRequired,
  image: PropTypes.string.isRequired,
}
BannerCallout.defaultProps = {
  variant: 'default',
}
export default BannerCallout
